.pui-page-alerts {
.alerts {
    display: grid;
    justify-items: center;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    height: 89%;

    .al-none {
        font-size: var(--fm-x-large);
        color: var(--pui-grey-text);
    }
}
.al-alert {
    .title {
        --fm-max-lines: 1;
        font-weight: bolder;
    }
    .content {
        --fm-max-lines: 2;
        color: var(--pui-text-description);
    }
    .date {
        color: var(--pui-grey-text);
        position: relative;
        left: 82%;
    }
}
}