:root {
    /* Font manager for Desktop */
	--fm-smaller: smaller;
    --fm-x-small: x-small;
    --fm-xx-small: xx-small;
	--fm-large: large;
	--fm-x-large: x-large;
	--fm-x-larger: 8.5vw;
	--fm-xx-large: xx-large;
	--fm-super-large: xxx-large;
}
ion-content.pui-bg.pui-split::part(scroll) {
    display: flex;
}
ion-content.pui-bg.pui-split {
    .pui-sidebar {
        width: 15vw;
        display: flex;
        align-items: center;
        justify-items: center;
        flex-direction: column;

        .category {
            border-top: 2px solid var(--pui-color-green);
            width: 90%;
            
            ion-item {
                border-radius: 80vw;
                --ion-icon-size: x-large;
                --ion-icon-color: var(--pui-color-green);
                ion-icon {
                    margin-right: 5px;
                }
            }
        }
    }
    .sidebar-part {
        width: 85vw;
        height: 100vh;
    }
}
.pui-hide-desktop {
    display: none !important;
}
* {
    overflow: hidden !important;
}

ion-content {
    .vz-video-container {
        padding: 10px;
        padding-top: 0px;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    .vz-video {
        width: 23.5vw;
        
        .vz-video-detail {
            .menu {
                width: 4vw;
                height: 2vw;
            }
            div {
                padding: 0px;

                .vz-text {
                    max-width: 81%;
                    line-height: 1em;
                    height: 2em;
                }
                ion-text.v {
                    font-size: var(--fm-x-small);
                }
            }
            .user {
                ion-text {
                    max-width: 52%;
                    font-size: var(--fm-smaller);
                }
                ion-button {
                    width: 28%;
                    height: 4vh;
                    right: 0%;
                    left: unset;
                    font-size: var(--fm-xx-small);
                }
            }
        }
        .duration {
            padding: 5px;
        }
        .vz-image {
            border-radius: 10px;
        }
        .vz-image::part(image) {
            border-radius: 2vh;
        }
    }
}
ion-item.selected {
    border-right: 9vw solid var(--pui-color-green);
}
ion-button {
    height: 4vh;
    width: 10vw;
}