// and (max-width: 1199px)
@media (min-width: 992px) {
    @import "desktop.scss";
}
@media (min-width: 768px) and (max-width: 991px){
    @import url(tablet.scss);
}
@media (max-width: 767px){
    @import url(mobile.scss);
}
body {
    font-family: cursive;
}
ion-text {
    display: block;
    font-family: system-ui;
}
ion-alert.pui-alert {
    --background: var(--pui-background);

    .alert-title {
        color: var(--pui-color-green);
    }
    .alert-message {
        color: var(--pui-text-color);
    }
    .alert-button {
        background: var(--pui-color-green);
        color: var(--pui-color-black);
        font-weight: bold;
        // border-radius: 51vw;
    }
}
ion-button {
    --background: var(--pui-color-green);
    --color: var(--pui-color-black);
    --border-radius: 60vw;
    border-radius: var(--border-radius);
    width: max-content;
}
ion-toolbar {
    display: flex;
}
ion-list, ion-item {
    --background: var(--pui-background);
    background-color: var(--pui-background) !important;
    --color: var(--pui-text-color);
}
ion-item {
    margin-bottom: 1vh;
    --border-style: none;
}
ion-item.selected {
    border-right: 9vw solid var(--pui-color-green);
}
ion-skeleton-text {
    background-color: var(--pui-skeleton);
}
ion-content {
    --background: var(--pui-background);
}
ion-text, ion-icon, ion-label {
    color: var(--pui-text-color);
}
ion-icon {
    color: var(--ion-icon-color);
    font-size: var(--ion-icon-size, unset);
}
ion-radio {
    --color-checked: var(--pui-color-green);
}
ion-toggle {
    height: 7vw;
    width: 11vw;
    --handle-width: 56%;
    --handle-height: 100%;
    --background: #3a0101;
    --background-checked: var(--pui-color-green);
    --handle-background: #ffffff;
    --handle-background-checked: var(--pui-color-white);
}
.pui-tab {
    .active {
        --color: var(--pui-color-red);
    }
    position: absolute;
    bottom: 3vh;
    height: 8vh;
    left: 14vw;
    width: 72vw;
    border-radius: 60vw;
    box-shadow: 0px 0px 20px var(--pui-shadow-color);
    --ion-icon-size: 8vw;
    color: var(--pui-grey-text);
    display: flex;
    background: var(--pui-background);
    align-items: center;
    justify-content: space-evenly;

    ion-button {
        height: 100%;
        width: calc( 72vw / 3);
    }
}
ion-list.sc-ion-select-popover {
    background-color: #111111;
}
ion-select {
    color: #00ffff;
}
ion-item.select-interface-option {
    --background: #111111;
    --color: #d5d5d5;
}
.pui-bg {
    background-color: var(--pui-background);
    --background: var(--pui-background);
}
.pui-searchbar {
    --cancel-button-color: #008900;
    --icon-color: #008900;
    width: 60%;
    --background: #121212;
    --color: #00ff00;
    --border-radius: 50vw;
}
.pui-image {
    object-fit: fill;
}
.ion-page {
    background-color: var(--pui-background);
    color: var(--pui-text-contrast);
}
ion-toolbar, .pui-app-toolbar {
    --background: var(--pui-background);
    --color: var(--pui-text-color);
    box-shadow: var(--pui-shadow-element);
    // height: 6.6vh;
    display: flex;
    align-items: center;
    min-height: 54px;
}
ion-back-button {
    display: block;
    color: var(--pui-text-color);
}
.pui-user-avatar {
    margin-right: 4px;
    min-width: 6vh;
    min-height: 6vh;
    max-width: 6vh;
    max-height: 6vh;
}
.pui-app-toolbar, ion-toolbar {
    ion-row {
        padding-left: 11px;
        height: 100%;

        ion-title:first-child {
            padding-left: 15px;
        }
    }
}
.pui-app-logo {
    width: 4vmax;
}

.pui-filter-buttons {
    .pui-filter-selected {
        --background: var(--pui-color-green);
        // border: 2px solid var(--pui-color-green);
        --color: var(--pui-color-black);
    }
    ion-button {
        --background: var(--pui-filter-buttons);
        --color: var(--pui-color-white);
        // border: 2px solid var(--pui-color-red);
        border-radius: var(--border-radius);
        font-size: var(--fm-smaller);
        --box-shadow: none;
    }
}
.pui-padding-10 {
    --padding-bottom: 10px;
    --padding-start: 10px;
    --padding-end: 10px;
    --padding-top: 10px;
    padding: 10px;
}
.pui-searchbar {
    height: 4vh;
    background-color: var(--pui-background);
    ion-searchbar {
        height: 100%;
        width: 100%;
        --border-radius: 50vw;
        --background: var(--pui-background);
        --color: var(--pui-text-color);
        --box-shadow: 0px 0px 4px var(--pui-shadow-color);
    }
    .results {
        position: relative;
        z-index: 30;
        background-color: var(--pui-background);
        border: 2px solid var(--pui-shadow-color);
        border-bottom-left-radius: 5vw;
        border-bottom-right-radius: 5vw;
        max-width: 100%;
        border-top: none;
        width: 100%;
    }
}
.pui-red {
    color: var(--pui-color-red);
    --color: var(--pui-color-red);
}
.pui-green {
    color: var(--pui-color-green);
    --color: var(--pui-color-green);
}
.pui-yellow {
    color: var(--pui-color-yellow);
    --color: var(--pui-color-yellow);
}
.pui-action-bar {
    .back-btn {
        width: 46px;
        height: 46px;
        --color: var(--pui-text-color);
        --box-shadow: none;
        --ion-icon-size: var(--fm-x-larger);
        display: flex;
        padding: 0px;
        --padding-start: 0px;
        --padding-end: 0px;
        --padding-bottom: 0px;
        --padding-top: 0px;
        --background: none;
        justify-content: center;
    }
    ion-title {
        padding: 0px;
    }
}
.pui-page-header {
    padding: 3vw;
    display: flex;
    align-items: center;
    align-content: center;

    #head {
        display: flex;
        align-items: center;
        align-content: center;
        width: 100%;
        padding-bottom: 1vh;
        border-bottom: 2px solid var(--pui-color-yellow);
        --ion-icon-size: var(--fm-xx-large);
        ion-icon.pui-red {
            padding: 0.6vw;
        }
    }
    .title {
        padding: 0px;
        font-size: 8vw;
    }
    .right {
        position: absolute;
        font-size: large;
        right: 3vw;
        color: #00ffff;
    }
}
.pui-section {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}
.pui-section .head {
    min-width: 94vw;
    display: flex;
    max-width: 94vw;
    position: relative;
    align-items: center;
    margin-bottom: 1vh;

    ion-text {
        font-size: var(--fm-x-large);
        color: var(--pui-color-yellow);
    }
    ion-select {
        color: #00ff00;
        font-size: var(--fm-x-large);
    }
}
.pui-padded-section {
    padding: 3vw;
    padding-top: 0vw;
}
.pui-sections {
    max-height: 100%;
}
ion-img::part(image) {
    border-radius: var(--border-radius);
}
ion-img {
    border-radius: var(--border-radius);
}
.pui-push-action {
    --background: var(--pui-color-green);
    --color: var(--pui-color-black);
}
.pui-push-action.pushed {
    border: 2px solid var(--pui-color-green);
    --color: var(--pui-color-green);
    --background: none;
    border-radius: var(--border-radius);
}

.pui-action-sheet.sheet-block {
    .action-sheet-button-inner {
        display: grid;
        justify-items: center;
        justify-content: center;
        align-items: center;
        align-content: center;
    }
    .action-sheet-icon {
        margin-right: 0px;
    }
    .action-sheet-button {
        width: 16vw;
        height: 16vw;
        border-radius: 50vw;
        padding: 0px;
        font-size: 0px;
        background-color: var(--pui-grey-text);
        display: flex;
        align-items: center;
        justify-content: center;
        --color: var(--pui-color-black);
        align-content: center;
    }
    .action-sheet-destructive {
        --color: var(--pui-color-red);
        position: absolute;
        background-color: var(--pui-background);
        left: -1%;
    }
    .action-sheet-button .action-sheet-button-inner ion-icon {
        --ion-icon-size: var(--fm-xx-large);
        font-size: var(--ion-icon-size);
        padding: 0px;
    }
    .action-sheet-group {
        display: flex;
        flex-wrap: wrap;
    }
}
.pui-action-sheet-blank {
    --backdrop-opacity: 0.5;
    --background: var(--pui-background);
    --button-color: var(--pui-grey-text);
    --color: var(--pui-text-color);

    .action-sheet-group {
        min-height: 20vh;
        border-radius: 4vw;
        border-top: 2px solid var(--pui-color-green);
    }
    .action-sheet-title {
        text-align: center;
        font-size: var(--fm-large);
        color: var(--pui-color-yellow);
    }
}
.pui-action-sheet {
    --backdrop-opacity: 0.5;
    --background: var(--pui-background);
    --button-color: var(--pui-grey-text);
    --color: var(--pui-text-color);

    .action-sheet-button-inner {
        width: 100%;
        height: 100%;
    }
    .action-sheet-title {
        min-width: 100%;
    }
    .action-sheet-group {
        min-height: 20vh;
        border-radius: 4vw;
        border-top: 2px solid var(--pui-color-green);

        .action-sheet-title {
            text-align: center;
            font-size: var(--fm-large);
            color: var(--pui-color-yellow);
        }
    }
}
.pui-sidebar {
    
}

.fm-line-2 {
    -webkit-line-clamp: 2;
}
.fm-line-1 {
    -webkit-line-clamp: 1;
}
.fm-text {
    word-break: break-all;
    -webkit-box-orient: vertical;
    display: -webkit-box;
    overflow: hidden;
    -webkit-line-clamp: var(--fm-max-lines);
}
.fm-full-text {
    --fm-max-lines: 0 !important;
}