ion-alert.cq-warning.pui-alert {
	.alert-button {
		background-color: var(--pui-color-red);
		color: var(--pui-color-white);
	}
}
.pui-page-clique {
	.vz-clique {
	width: 94vw;
	margin-bottom: 2vh;
	padding-bottom: 2vh;
	border-bottom: 2px solid grey;

	.cq-title {
		margin-bottom: 1vh;
		display: flex;
		align-items: center;
	}
	.cq-title ion-text {
		color: var(--pui-color-yellow);
		font-size: var(--fm-x-large);
		--fm-max-lines: 1;
	}
	.cq-button {
		--box-shadow: none;
		--background: transparent;
		--ion-icon-color: var(--pui-color-yellow);
		--ion-icon-size: var(--fm-x-large);
	}
	.danger {
		--ion-icon-color: var(--pui-color-red);
	}
	.cq-members {
		overflow-x: scroll;
		display: flex;
		ion-avatar.active {
			// border: 2px solid var(--pui-color-green);

			.indicator {
				display: block;
				width: 4vw;
				height: 4vw;
				background-color: var(--pui-color-green);
				border-radius: 50vw;
				position: absolute;
				bottom: 0%;
				right: 0%;
			}
		}
		ion-avatar {
			position: relative;
			width: 15vw;
			height: 15vw;
			min-width: 15vw;
			min-height: 15vw;
			margin-right: 2vw;
			.indicator {
				display: none;
			}
		}
	}
}
.cq-fab {
	--background: var(--pui-color-green);
	--ion-icon-color: var(--pui-color-black);
	--ion-icon-size: var(--fm-xx-large);
}
}
.ls-done {
	--color: var(--pui-color-red);
	--background: none;
	--box-shadow: none;
}