.pui-page-upload {
    .base {
        padding: 3vw;
    }
    .heading {
        font-size: var(--fm-x-larger);
        color: var(--pui-color-yellow);
        text-align: center;
    }
    .up-btn {
        position: absolute;
        bottom: 8vh;

        .next, .back {
            height: 5vh;
            position: absolute;
            width: 25vw;
            --background: none;
            color: var(--pui-color-red);
            --box-shadow: none;
        }
        .back {
            left: 3vw;
        }
        .next {
            left: calc( 100vw - 25vw - 5vw);
        }
    }
    .image-post {
        position: absolute;
        top: calc( 50% - ( 26vh / 2) );

        div {
            position: absolute;
            background-color: lighten($color: #000000, $amount: 15);
            width: 100%;
            height: 100%;
            --ion-font-size: var(--fm-super-large);
            font-size: var(--ion-font-size);
            display: flex;
            align-items: center;
            justify-content: center;
            color: var(--pui-color-green);
        }
        div, ion-img {
            width: 94vw;
        } 
        div:active {
            background-color: darken($color: #00ff00, $amount: 10);
        }
    }
}