.vz-player {
    .vjs-play-progress, .vjs-volume-level {
        background-color: var(--pui-color-green);
        color: var(--pui-color-green);
    }
    .vjs-icon-placeholder:before, .vjs-button>.vjs-icon-placeholder:before {
        text-align: center;
        color: var(--pui-color-green);
    }
    .vjs-big-play-button {
        border: none;
        z-index: 50;
        width: 12vw;
        border-radius: 50vw;
        height: 12vw;
        top: 42%;
        left: 44vw;
        display: none;
    }
    .vjs-loading-spinner {
        z-index: 50;
        border: 4px solid var(--pui-color-red);
    }
    .vjs-loading-spinner:before, .vjs-loading-spinner:after {
        border-top-color: var(--pui-color-green);
        border-color: var(--pui-color-red);
        margin: -4px;
    }
    .vjs-control-bar {
        height: 100%;
        display: flex;
        align-items: flex-end;
        
        * {
            margin-bottom: 0.5em !important;
        }
        .vjs-control {
            height: 3.4em;
        }
        .vjs-progress-control {
            position: absolute;
            bottom: 3em;
            width: 100vw;
            height: 2em;
        }
    }
    .vjs-volume-panel {
        display: none;
    }
    .vjs-play-control {
        left: calc( 50% - 1.7em );
        border: 2px solid var(--pui-color-green);
    }
    .vjs-fullscreen-control {
        position: absolute;
        left: 88%;
    }
    .vjs-picture-in-picture-control {
        position: absolute;
        left: 3%;
    }
    .vjs-remaining-time {
        display: none;
    }
    .vjs-current-time, .vjs-duration {
        display: block;
        position: absolute;
        bottom: 4.5em;    
    }
    .vjs-duration {
        right: 0%;
    }
    button.vjs-button[type=button] {
        border-radius: 50vw;
        height: 3.4em;
        width: 3.4em;
        text-align: center;
    }
}
