:root {
	/* PUI Color Definitions */

	--pui-color-green: #00bb00;
	--pui-color-red: #ff0000;
	--pui-color-yellow: #a5a500;
	--pui-color-purple: #ff00ff;
	--pui-color-blue: #0000ff;
	--pui-color-black: #000000;

	--pui-color-white: #ffffff;

	--pui-background: #ffffff;
	--pui-text-color: #000000;

	--pui-grey-200: #afafaf;
	--pui-grey-700: #303030;

	--pui-grey-text: #777777;
	--pui-text-description: #a5a5a5;
	--pui-text-subtitle: #5a5a5a;

	--pui-shadow-color: #000000;
	// --pui-shadow-color: #00ff00;

	/* Font Manager Styles (--fm) */

	--fm-smaller: 3.6vw;
	--fm-large: 4.8vw;
	--fm-x-large: 6.2vw;
	--fm-x-larger: 8.5vw;
	--fm-xx-large: 11.3vw;
	--fm-super-large: 31.3vw;
}
body {

	--pui-setting-background: #0c0c0c;
	--pui-shadow-element: 0px 1px 3px var(--pui-color-green);

	--pui-mobile-only: var(--pui-grey-200);

	--pui-link: var(--pui-color-green);
	--pui-link-hover: #00cc00;
	--pui-link-active: #00b300;

	--pui-button-bg: var(--pui-color-green);
	--pui-button-text: var(--pui-text-color);

	--pui-filter-buttons: var(--pui-grey-200);

	--pui-skeleton: #cccccc;
}

