body.pui-dark-mode {
	--pui-color-green: #00ff00;
	--pui-color-yellow: #ffff00;

	--pui-background: #000000;
	--pui-text-color: #ffffff;

	--ion-background-color: #000000;
	--ion-background-color-rgb: 0,0,0;

	--ion-text-color: #ffffff;
	--ion-text-color-rgb: 255,255,255;

	--pui-text-description: #cccccc;
	--pui-text-subtitle: #dddddd;
	--pui-mobile-only: #2a2a2a;

	--pui-shadow-color: var(--pui-color-green);
	--pui-setting-background: #0c0c0c;
	--pui-filter-buttons: var(--pui-grey-700);
	
	--pui-skeleton: #ffffff;
	
	--pui-mobile-only: #080808;
}

.pui-scrollable::-webkit-scrollbar-thumb {
	background: var(--pui-color-green)
}

.pui-scrollable {
	scrollbar-color: var(--pui-color-green) var(--pui-background);
	border-radius: 40vw;
}