.pui-page-player {
    .btn-list {
        display: flex;
        justify-content: space-evenly;
        --ion-icon-size: var(--fm-x-large);

        .pl-btn {
            height: 5vh;
            width: 5vh;
        }
    }
    .vz-info {
        padding: 10px;
        display: flex;
        align-items: center;
    }
    .userName {
        font-weight: bolder;
        font-size: var(--fm-large);
        max-width: 49vw;
        --fm-max-lines: 1;
    }
    .follows {
        color: var(--pui-grey-text);
        font-size: var(--fm-smaller);
    }
    .follow {
        height: 5vh;
        width: 27vw;
        font-size: var(--fm-smaller);
    }
    .more {
        font-size: var(--fm-x-large);
        color: var(--pui-color-black);
        font-weight: bolder;
        text-align: center;
        margin-top: 2vh;
        margin-bottom: 1vh;
        background-color: var(--pui-color-green);
        border-radius: 4vw;
        height: 1.2em;
    }
    .text-center {
        display: grid;
        justify-items: center;
        justify-content: center;
        text-align: center;
    }
    .description {
        --fm-max-lines: 4;
        color: var(--pui-text-subtitle);
        max-width: 96vw;
        margin-bottom: 3vh;
    }
    .title {
        font-size: var(--fm-xx-large);
        --fm-max-lines: 2;
        font-weight: bolder;
        max-width: 90vw;
    }
    #vz-player {
        width: 100vw;
        height: 26vh;
    }
    .pl-btn.done {
        color: var(--pui-color-red);
    }
    .comment-pane {
        display: flex;
        width: 100vw;
        overflow-x: scroll;
        padding-left: 3vw;
        padding-right: 3vw;
        
        .pui-user-avatar {
            margin-right: 5vw;
        }
        .pui-user-avatar:last-child {
            margin-right: 0px;
        }
    }
    .vo-player {
        height: 26vh;
        width: 100vw;
        background-color: lightblue;
    }
}