.pui-page-profile {
    .section1 {
        .background {
            background-color: var(--pui-color-red);
            height: 20vh;
        }
        .content {
            display: grid;
            justify-items: center;
            justify-content: center;
            text-align: center;
            position: relative;
            bottom: 8vh;
            .displayName {
                font-size: var(--fm-xx-large);
                --fm-max-lines: 1;
                max-width: 90vw;
            }
            .userName {
                color: var(--pui-grey-text);
            } 
            .userName, .description {
                font-size: var(--fm-large);
            }
            .description {
                max-width: 86vw;
                color: var(--pui-text-description);
                --fm-max-lines: 3;
            }
            ion-text {
                margin-bottom: 1vh;
            }
            .act-btn {
                --padding-bottom: 2.5vw;
                --padding-end: 2.5vw;
                --padding-start: 2.5vw;
                --padding-top: 2.5vw;
                --ion-icon-size: var(--fm-xx-large);
                width: 13vw;
                height: 13vw;
                --background: transparent;
                --box-shadow: none;
                --color: var(--pui-text-color);
            }
            .follow {
                height: 13vw;
                margin-left: 10vw;
                margin-right: 10vw;
            }
            .follow.act-btn {
                margin-left: 15vw;
                margin-right: 15vw;
            }
            .followed {
                --background: none;
            }
            .following {
                font-weight: bolder;
                font-size: var(--fm-large);
                color: var(--pui-text-subtitle);
            }
            .image {
                width: 39vw;
                --border-radius: 50vw;
                border: 2px solid var(--pui-color-black);
            }
        }
    }
    .section2 {
        padding-left: 4vw;
        padding-right: 4vw;
    }
}