.pui-page-home {
.hm-button-list {
    --ion-icon-size: var(--fm-x-large);
    position: relative;
    left: 75vw;
    color: var(--pui-text-color);
    height: 100%;
}
.hm-toolbar {
    ion-row {
        display: flex;
        justify-content: center;
        align-items: center;
    }
    ion-avatar.pui-user-avatar {
        min-width: 5vh;
        min-height: 5vh;
        max-width: 5vh;
        max-height: 5vh;
    }
    .hm-btn {
        --padding-bottom: 0px;
        --padding-end: 0px;
        --padding-start: 0px;
        --padding-top: 0px;
        --ion-icon-size: var(--fm-x-large);
        --background: none;
        --color: var(--pui-text-color);
        --box-shadow: 0px 1px 3px var(--pui-color-green);
    }
    .pui-app-logo {
        --margin-side: 30vw;
        margin-left: var(--margin-side);
        margin-right: var(--margin-side);
    }
}
}