.pui-page-library {
.lb-section {
    // border-bottom: 2px solid #585858;
}
.vz-video {
    height: 38vh;
    min-width: 74vw;
    padding-bottom: 0px;
    margin-right: 20px;

    .menu {
        left: 65vw;
    }
    .vz-text {
        max-width: 64vw;
    }
}
.down {
    width: 74vw;
    height: 20vh;
    background-color: #000000ad;
    // opacity: 0.8;
    border-radius: 4vw;
    position: absolute;
    top: 0%;
    display: none;
    backdrop-filter: blur(3px);
}
.user ion-text {
    width: 46vw;
}
.dload {
    color: var(--pui-color-green);
    padding: 5vw;
    border-radius: 50vw;
    width: 4vh;
    height: 4vh;
    display: none;
    text-align: center;
    font-size: var(--fm-smaller);
    align-items: center;
    justify-content: center;
}
.vz-video.downloading {
    .dload {
        display: flex;
    }
    .down {
        display: block;
    }
    .check {
        display: none;
    }
}
.check {
    --ion-icon-size: 6vw;
    color: var(--pui-color-green);
    left: 5vw;
    position: relative;
}
.vz-video:last-child {
    margin-right: 0px;
}
.lb-deleteBin {
    color: var(--pui-color-red);
    --background: none;
    --ion-icon-size: var(--fm-large);
    --padding-start: 0px;
    --padding-bottom: 0px;
    --padding-end: 0px;
    --padding-top: 0px;
    width: 8vw;
    height: 8vw;
    position: absolute;
    right: 0%;
    margin-bottom: 0vh;
}
.lb-persona {
    width: 30vw;
    height: max-content;
    margin-bottom: 24px;
    margin-right: 10px;

    .vid {
        width: 30vw;
        height: 27vh;
        position: relative;
        background-color: var(--pui-color-black);
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 4vw;
        background-color: #b9b9b970;
        margin-bottom: 3px;

        .bdrop {
            // box-shadow: 0px 2px 4px var(--pui-color-green);
            position: absolute;
            top: 0%;
            background-color: #000000ad;
            width: 100%;
            height: 100%;
            border-radius: 4vw;
            backdrop-filter: blur(3px);
            .duration {
                background-color: var(--pui-color-black);
                color: var(--pui-color-white);
                position: absolute;
                bottom: -2%;
                left: 7%;
                padding: 6px;
                font-size: var(--fm-smaller);
                border-radius: 50vw;
            }
            ion-button.menu {
                position: absolute;
                bottom: 2%;
                right: -1%;
                color: var(--pui-color-green);
                width: 7vw;
                height: 7vw;
                --background: none;
                --box-shadow: none;
                --ion-icon-size: var(--fm-large);
                --padding-bottom: 0px;
                --padding-end: 0px;
                --padding-start: 0px;
                --padding-top: 0px;
            }
            ion-avatar {
                width: 10vw;
                height: 10vw;
                position: absolute;
                top: 4%;
                left: 6%;
            }
        }
    }
    ion-text {
        --fm-max-lines: 3;
    }
}
.lb-persona:last-child {
    margin-right: 0px;
}
}