.vz-video-detail {
    padding: 0px;
    position: relative;

    div ion-text.v {
        color: var(--pui-grey-text);
        font-size: var(--fm-smaller);
    }
    div .vz-text {
        max-width: 80vw;
        --fm-max-lines: 2;
    }
    div {
        padding: 2vw;
    }
    .menu {
        --padding-bottom: 1vw;
        --padding-end: 1vw;
        --padding-start: 1vw;
        --padding-top: 1vw;
        font-size: var(--fm-xx-large);
        height: 9vw;
        width: 9vw;
        --color: var(--pui-text-color);
        --background: transparent;
        --box-shadow: none;
        position: absolute;
        top: 0%;
        right: 0%;
    }
    .user {
        display: flex;
        align-items: center;
        height: 7vh;
        
        ion-text {
            font-size: var(--fm-large);
            color: var(--pui-grey-text);
            max-width: 50vw;
            --fm-max-lines: 1;
        }
        ion-button {
            height: 5vh;
            width: 27vw;
            font-size: var(--fm-smaller);
            left: 65vw;
            position: absolute;
        }
    }
}

.vz-image {
    border-radius: 4vw;
}
.vz-image::part(image) {
    border-radius: 4vw;
}
.vz-text {
    font-size: var(--fm-large);
}
.vz-video-container {
    justify-items: center;
    padding: 4vw;
}
.vz-video {
    position: relative;
    width: 100%;
    height: max-content;
    padding-bottom: 2vh; 

        .fab {
            position: absolute;
            top: 0%;
            left: 87%;
            width: 10%;
            --padding-bottom: 0px;
            --padding-end: 0px;
            --padding-start: 0px;
            --padding-top: 0px;
            display: flex;
            flex-direction: column;
            align-items: center;
            --color: #00ff00;
            --background: rgba(0,0,0,0.7);
        }
        .duration {
            position: absolute;
            left: 3%;
            bottom: 4%;
            margin: 0px;
            height: min-content;
            background-color: var(--pui-color-black);
            color: var(--pui-color-white);
            padding: 8px;
            font-size: var(--fm-smaller);
            border-radius: 50vw;
        }
}