.pui-page-settings {
    .sign {
        position: absolute;
        right: 3vw;
        --ion-icon-size: var(--fm-x-large);
        font-size: var(--fm-smaller);
    }
    .vz-setting {
        position: relative;
        display: flex;
        .title {
            font-weight: bolder;
            font-size: var(--fm-x-large);
        }
    }
    .vz-setting.toggle {
        ion-toggle {
            position: absolute;
            right: 2%;
        }
    }
    .vz-setting.input {
        .value {
            position: absolute;
            right: 2%;
            max-width: 80vw;
            --fm-max-lines: 1;
            color: var(--pui-grey-text);
        }
    }
}