.nc-full-x {
    width: 100%;
}
.nc-medium-box {
    width: 100%;
}

// Every other style is global and is for portrait mobile

// Extra large (Televisions)
@media (min-width: 1200px) {
}

// Large (Desktops)
@media (min-width: 992px) and (max-width: 1199px) {
    .nc-medium-box {
        width: 100%;
    }
}

// Medium (Tablets)
@media (min-width: 768px) and (max-width: 991px) {
    .nc-medium-box {
        width: 50%;
    }
}

// Small (Landscape Mobile)
@media (min-width: 576px) and (max-width: 767px) {
    .nc-medium-box {
        width: 100%;
    }
}